namespace $ {
	export class $milkywaystd_ui_treemenu_demo extends $mol_example_large {
		
		/**
		 * ```tree
		 * title \Tree menu
		 * ```
		 */
		title() {
			return "Tree menu"
		}
		
		/**
		 * ```tree
		 * tree_menu_items /
		 * 	<= p1
		 * 	<= p2
		 * ```
		 */
		tree_menu_items() {
			return [
				this.p1(),
				this.p2()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * sub / <= menu
		 * ```
		 */
		sub() {
			return [
				this.menu()
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * tags / \menu
		 * ```
		 */
		tags() {
			return [
				"menu"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * p1 *
		 * 	label \label 1
		 * 	link * a \0
		 * ```
		 */
		p1() {
			return {
				label: "label 1",
				link: {
					a: "0"
				} as Record< string, any >
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * p22 *
		 * 	label \sub link 2
		 * 	link * a \1
		 * ```
		 */
		p22() {
			return {
				label: "sub link 2",
				link: {
					a: "1"
				} as Record< string, any >
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * p2 *
		 * 	label \label 2
		 * 	items / <= p22
		 * ```
		 */
		p2() {
			return {
				label: "label 2",
				items: [
					this.p22()
				] as readonly any[]
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * menu $milkywaystd_ui_treemenu items <= tree_menu_items
		 * ```
		 */
		@ $mol_mem
		menu() {
			const obj = new this.$.$milkywaystd_ui_treemenu()
			
			obj.items = () => this.tree_menu_items()
			
			return obj
		}
	}
	
}

