namespace $ {
	export class $examples_crudapi extends $mol_book2_catalog {
		
		/**
		 * ```tree
		 * menu_title \CRUDAPI examples
		 * ```
		 */
		menu_title() {
			return "CRUDAPI examples"
		}
		
		/**
		 * ```tree
		 * spreads *
		 * 	p <= products
		 * 	p2 <= products2
		 * ```
		 */
		spreads() {
			return {
				p: this.products(),
				p2: this.products2()
			} as Record< string, any >
		}
		
		/**
		 * ```tree
		 * tags / \crud
		 * ```
		 */
		tags() {
			return [
				"crud"
			] as readonly any[]
		}
		
		/**
		 * ```tree
		 * products $examples_products
		 * 	param \mol_book2_crud_demo_1
		 * 	menu_title \Classic
		 * ```
		 */
		@ $mol_mem
		products() {
			const obj = new this.$.$examples_products()
			
			obj.param = () => "mol_book2_crud_demo_1"
			obj.menu_title = () => "Classic"
			
			return obj
		}
		
		/**
		 * ```tree
		 * products2 $examples_products2
		 * 	param \mol_book2_crud_demo_2
		 * 	menu_title \Optimistic
		 * ```
		 */
		@ $mol_mem
		products2() {
			const obj = new this.$.$examples_products2()
			
			obj.param = () => "mol_book2_crud_demo_2"
			obj.menu_title = () => "Optimistic"
			
			return obj
		}
	}
	
}

